  setup() {
    let hireBtn: Element | null;
    onMounted(() => {
      hireBtn = document.querySelector(".home-hire-me-wrapper");
    });

    const transition = () => {
      if (!hireBtn) {
        return;
      }
      console.log("here");
      // hireBtn.classList.remove('home-hire-me-wrapper--mouseleave');
      hireBtn.classList.add("home-hire-me-wrapper--mouseleave");
    };
    return {
      transition,
    };
  },
};
